.text-adjust {
  margin-bottom: -2.5px;
}

.project-info a {
  color: #7ee4e8;
}

strong, b {
  font-family: "GT-Walsheim Medium";
  font-style: normal;
  font-weight: normal;
}

p {
  margin: 0 0 25px;
  font-size: 1.125rem;
}

p strong, p b {
  font-family: "GT-Walsheim Medium";
  font-style: normal;
  font-weight: normal;
}

p a {
  text-decoration-color: #958F86;
  position: relative;
  color: #1328e6;
  z-index: 0;
  white-space: initial;
}

@media (min-width: 1280px) {
  p a {
    white-space: nowrap;
  }
}

p a:after {
  content: "";
  position: absolute;
  height: 2px;
  right: 0;
  bottom: 13px;
  left: 0;
  margin-bottom: -16px;
  background-image: linear-gradient(to right, #7ee4e8, #1328e6);
  z-index: -1;
  transition: opacity 0.5s linear;
  opacity: 0;
}

p a:hover:after {
  opacity: 1;
}

.wysiwyg {
  max-width: 100%;
}

.wysiwyg p {
  margin: 0 0 25px;
  font-size: 1.25rem;
}

.wysiwyg p strong, .wysiwyg p b {
  font-family: "GT-Walsheim Medium";
  font-style: normal;
  font-weight: normal;
}

.wysiwyg a {
  text-decoration-color: #958F86;
  position: relative;
  color: #1328e6;
  z-index: 0;
  white-space: initial;
}

@media (min-width: 1280px) {
  .wysiwyg a {
    white-space: normal;
  }
}

.wysiwyg a:after {
  content: "";
  position: absolute;
  height: 2px;
  right: 0;
  bottom: 13px;
  left: 0;
  margin-bottom: -16px;
  background-image: linear-gradient(to right, #7ee4e8, #1328e6);
  z-index: -1;
  transition: opacity 0.5s linear;
  opacity: 0;
}

.wysiwyg a:hover::after {
  opacity: 1;
}

.wysiwyg strong, .wysiwyg b {
  font-family: "GT-Walsheim Medium";
  font-style: normal;
  font-weight: normal;
}

.wysiwyg ul {
  font-size: 1.25rem;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  padding: 0 1.125em;
}

.wysiwyg ul li {
  list-style: outside;
  list-style-position: inside;
  margin-left: 2px;
  margin-bottom: .5em;
}

.wysiwyg h1, .wysiwyg h2, .wysiwyg h3, .wysiwyg h4, .wysiwyg h5 {
  font-family: "GT-Walsheim Medium";
  margin-bottom: 15px;
}

.wysiwyg td {
  padding-right: 15px;
}

.wysiwyg img {
  margin-bottom: 25px;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.container.narrow {
  max-width: 1280px;
}

hr {
  background: #004270;
  height: 1px;
  width: 100%;
}

hr.blocks {
  background: #004270;
  height: 1px;
}

hr.primary {
  background: #004270;
  height: 1px;
  border: 0;
}

hr.secondary {
  background: #FFD53F;
  height: 1px;
  border: 0;
}

iframe {
  min-width: 100%;
}

.table, table {
  background: transparent;
}

.table strong, table strong {
  font-family: "GT-Walsheim Medium";
  color: #004270;
}

.table td, .table th, table td, table th {
  border: 0px;
  padding: 10px;
}

.table.is-narrow td, .table.is-narrow th, table.is-narrow td, table.is-narrow th {
  border: 0px;
  padding: 10px;
}

.z-1 {
  z-index: 1;
}

.full-width-img {
  height: 60vh;
  object-fit: cover;
  object-position: center;
}

.lazyload,
.lazyloading {
  opacity: .0;
}

.lazyloaded {
  opacity: 1;
  transition: opacity 1.5s;
}

.chevron-bottom:before {
  border-style: solid;
  border-width: 2px 2px 0 0;
  content: '';
  display: inline-block;
  height: 16px;
  width: 16px;
  top: 7px;
  left: 0;
  position: relative;
  transform: rotate(-45deg);
  vertical-align: top;
}

.chevron-right:before {
  border-style: solid;
  border-width: 1px 1px 0 0;
  border-color: #000;
  content: '';
  display: inline-block;
  height: 16px;
  width: 16px;
  top: 7px;
  left: 0;
  position: relative;
  transform: rotate(45deg);
  vertical-align: top;
}

.chevron-left:before {
  border-style: solid;
  border-width: 1px 1px 0 0;
  border-color: #000;
  content: '';
  display: inline-block;
  height: 16px;
  width: 16px;
  top: 7px;
  right: -6px;
  position: relative;
  transform: rotate(-130deg);
  vertical-align: top;
}

.go-top {
  border-radius: 5px;
  background-color: #010e1b;
  background-color: rgba(1, 14, 27, 0.7);
  position: fixed;
  width: 45px;
  height: 45px;
  display: block;
  right: 15px;
  bottom: 15px;
  border: none;
  opacity: 0;
  z-index: -1;
  transition: all .4s ease-in;
}

.go-top:hover {
  opacity: 1;
  background-color: #010e1b;
  background-color: rgba(1, 14, 27, 0.9);
}

.go-top.is-active {
  opacity: .8;
  z-index: 4;
}

.embed-responsive {
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  overflow: hidden;
}

.embed-responsive .embed-responsive-item,
.embed-responsive iframe,
.embed-responsive embed,
.embed-responsive object,
.embed-responsive video {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.embed-responsive-16by9 {
  padding-bottom: 56.25%;
}

.embed-responsive-4by3 {
  padding-bottom: 75%;
}

.text-xxs {
  font-size: .55rem;
}

@media (max-width: 1460px) and (min-width: 992px) {
  .hide-small-desktop {
    display: none !important;
  }
}

.featured_insight {
  height: auto;
}

@media (min-width: 992px) {
  .featured_insight {
    height: 450px;
  }
}

@media (min-width: 1280px) {
  .featured_insight {
    height: 525px;
  }
}

@media (min-width: 992px) {
  .insights-fill-height {
    display: grid;
    grid-gap: 10.3px;
    grid-template-columns: repeat(auto-fill, minmax(425px, 1fr));
    grid-auto-rows: 10px;
  }
  .insights-fill-height .insight {
    margin: 0;
    display: grid;
    grid-template-rows: 1fr auto;
  }
}

@media (min-width: 992px) {
  .insights-top-align {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .insights-top-align .item {
    width: calc(100% / 3);
  }
}

.hover-shadow {
  transition: box-shadow 0.3s ease-in-out;
}

.hover-shadow:hover {
  box-shadow: 1px 1px 2px 3px #eee;
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

html, body {
  font-family: "GT-Walsheim Regular";
  background-color: #fff;
}

.bkg-gradient {
  background: linear-gradient(180deg, #fff 0%, #F8F5F2 100%);
}
